import { queryRegist } from '../../api'
export default {
  data() {
    return {
      nameFormat: true, //昵称报错
      emailFormat: true, //邮箱报错
      userInfo:{
        userName: '',
        email: '',
        QQ: '',
        password: '',
      },
        password_confirm: ''

    }
  },
  methods: {
    signup() {
      if (!this.checkFormat()|| !this.nameFormat || !this.emailFormat) {
        return
      }
      // 请求数据，查询注册结果
      queryRegist(this.userInfo).then(res=>{
        if(res[1].data.infoCode ==="500"){
          uni.showToast({
            icon:'none',
            title:res[1].data.infoText
          })
        }else{
          uni.showToast({
            icon:'none',
            title:'注册成功'
          })
          //延迟两秒自动跳转
          setTimeout(() => this.toLogin(), 2000);
        }
      })
      // data{
      //   currWeek:null,
      //   groupId:0,
      //   groupName:null,
      //   groups:null,
      //   infoCode :"200",
      //   infoText:"注册成功",
      //   status:null,
      //   userId:727,
      //   userName:"test"
      // }
    },
    toLogin() {
      uni.reLaunch({url: "../login/login"})
    },
    checkFormat() {
      //检查是否有项目为空
      if (!this.userInfo.userName) {
        uni.showToast({
          icon: 'none',
          title: '请填写用户名再注册'
        });
        return false;
      } else if (!this.userInfo.email) {
        uni.showToast({
          icon: 'none',
          title: '请填写邮箱再注册'
        });
        return false;
      } else if (!this.userInfo.QQ) {
        uni.showToast({
          icon: 'none',
          title: '请填写qq号再注册'
        });
        return false;
      } else if (!this.userInfo.password) {
        uni.showToast({
          icon: 'none',
          title: '请填写密码再注册'
        });
        return false;
      } else if (!this.password_confirm) {
        uni.showToast({
          icon: 'none',
          title: '请填写确认密码再注册'
        });
        return false;
      } else if (this.userInfo.password !== this.password_confirm) {
        uni.showToast({
          icon: 'none',
          title: '密码不正确，请重新填写'
        });
        return false;
      } else {
        return true;
      }
    },
    checkEmail() {
      let e = /^([a-zA-Z0-9.-])+\@(([a-zA-Z0-9-])+.)+([a-zA-Z0-9]{2,4})+$/;
      this.emailFormat = e.test(this.userInfo.email);
    },
    //验证昵称长度，4-16为正确
    checkName() {
      if (this.userInfo.userName.length > 16 || this.userInfo.userName.length < 4) {
        this.nameFormat = false;
      } else {
        this.nameFormat = true;
      }
    }
  }
}
